import React from "react";
import PropTypes from "../utils/propTypes";
import bn from "../utils/bemnames";
import { Breadcrumb, BreadcrumbItem, Button, Col, Row } from "reactstrap";
import Typography from "./Typography";
import { MdDelete, MdEdit } from "react-icons/md";

const bem = bn.create("page");

const Page = ({
  title,
  breadcrumbs,
  tag: Tag,
  className,
  children,
  editable,
  ...restProps
}) => {
  const classes = bem.b("px-3", className);

  return (
    <Tag className={classes} {...restProps}>
      <Row>
        <Col md={9} sm={12} xs={12}>
          <div className={bem.e("header")}>
            {title && typeof title === "string" ? (
              <Typography type="h4" className={bem.e("title")}>
                <i>{title}</i>
              </Typography>
            ) : (
              title
            )}
            {breadcrumbs && (
              <Breadcrumb size="lg" className={bem.e("breadcrumb")}>
                {/* <BreadcrumbItem>Home</BreadcrumbItem> */}
                {breadcrumbs.length &&
                  breadcrumbs.map(({ name, active }, index) => (
                    <BreadcrumbItem key={index}>
                      <small>{name}</small>
                    </BreadcrumbItem>
                  ))}
                {breadcrumbs.length &&
                  breadcrumbs.map(({ name1, active }, index) => (
                    <BreadcrumbItem key={index} active={active}>
                      <small>{name1}</small>
                    </BreadcrumbItem>
                  ))}
              </Breadcrumb>
            )}
          </div>
        </Col>
        {editable && (
          <Col md={3} sm={12} xs={12} className="mt-1">
            <Button size="sm" className="pr-5 pl-5 m-2" outline>
              <MdEdit />
            </Button>
            <Button size="sm" className="pr-5 pl-5" outline color="danger">
              <MdDelete />
            </Button>
          </Col>
        )}
      </Row>
      {children}
    </Tag>
  );
};

Page.propTypes = {
  tag: PropTypes.component,
  title: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  className: PropTypes.string,
  children: PropTypes.node,
  editable: PropTypes.bool,
  breadcrumbs: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      active: PropTypes.bool,
    })
  ),
};

Page.defaultProps = {
  tag: "div",
  title: "",
};

export default Page;
